                                <button class="orderCopy-l" @click.stop="copyBtn(OrderDetailData.billNo)">复制</button>
                            </div>
                        </div>
                        <div class="detailsItem-l" v-if="parentData.flag!= 1&&parentData.flag!= 6">
                            <div class="detailsLeft-l">支付方式</div>
                            <div class="detailsRight-l">{{OrderDetailData.payType}}</div>
                        </div>
                        <div class="detailsItem-l" v-if="parentData.flag!= 1&&parentData.flag!= 6">
                            <div class="detailsLeft-l">支付时间</div>
                            <div class="detailsRight-l">{{OrderDetailData.payTime}}</div>
                        </div>
                        <div class="detailsItem-l">
                            <div class="detailsLeft-l">创建时间</div>
                            <div class="detailsRight-l">{{OrderDetailData.createdTime}}</div>
                        </div>
                        <div class="detailsItem-l">
                            <div class="detailsLeft-l">可获积分</div>
                            <div class="detailsRight-l">+ {{OrderDetailData.pointIncome}}</div>
                        </div>
                    </div>
                    <!--优惠信息-->
                    <div class="detailsInfo-l discountsInfo-l">
                        <div class="detailsItem-l">
                            <div class="detailsLeft-l">订单总额</div>
                            <div class="detailsRight-l" v-if="orderDataIsReady">￥{{Number(OrderDetailData.billTotal).toFixed(2)}}</div>
                        </div>
                        <div class="detailsItem-l" v-show="OrderDetailData.billType2==0">
                            <div class="detailsLeft-l">优惠券</div>
                            <div class="detailsRight-l" v-if="orderDataIsReady">-￥{{Number(OrderDetailData.couponTotal).toFixed(2)}}</div>
                        </div>
                        <div class="detailsItem-l" v-show="OrderDetailData.billType2==0">
                            <div class="detailsLeft-l">积分抵扣</div>
                            <div class="detailsRight-l" v-if="orderDataIsReady">-￥{{(Number(OrderDetailData.pointCost / 100)).toFixed(2)}}</div>
                        </div>
                        <div class="detailsItem-l">
                            <div class="detailsLeft-l">配送费</div>
                            <div class="detailsRight-l" v-if="orderDataIsReady">-￥{{Number(OrderDetailData.deliveryTotal).toFixed(2)}}</div>
                        </div>
                        <div class="line-l"></div>
                        <div class="detailsMoney-l">
                            <div class="detailsLeft-l">实付金额：</div>
                            <div class="detailsRight-l">
                                <span>￥</span>
                                <span class="pocketNum-l" v-if="orderDataIsReady">{{Number(OrderDetailData.realPayTotal).toFixed(2)}}</span>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="fixedHeight-l" :style="{height:50+44+this.$footBarHeight+'px'}"></div>
            </div>

        </div>

        <div class="fixedBox" v-if="(parentData.flag!= 6&&parentData.flag!= 9)&&(notClick||parentData.flag != 1) && themeCurrent == '0'" :style="{paddingBottom:this.$footBarHeight+'px'}">
            <div class="relationBtn" v-if="OrderDetailData.shopList.length==1" @click="callProperty()">
                <img class="relationImg" src="@/assets/images/mall/call_small.png" alt="">
                <div class="relationTxt">联系商家</div>
            </div>
            <div class="relationBtn" v-if="OrderDetailData.shopList.length!=1">

            </div>
            <div class="fixedBtnList" v-if="parentData.flag == 1&&notClick">
                <div class="removeBtn" @click="orderCancel()">取消订单</div>
                <div class="gotopayBtn" @click="prepareToPayOrder()">去支付</div>
            </div>
            <div class="fixedBtnList" v-if="parentData.flag == 3&&notClick">
                <div class="gotopayBtn" @click="confirmOrder()">确认收货</div>
            </div>
            <div class="fixedBtnList" v-if="parentData.flag == 7&&notClick">
                <div class="removeBtn" @click="refundBySub()">申请退款</div>
            </div>
            <div class="fixedBtnList" v-if="parentData.flag == 4">
<!--                <div class="removeBtn" @click="deleteOrder()" ng-if="notClick">删除订单</div>-->
                <div class="evaluateBtn" @click="goToProduct(1)" v-show="OrderDetailData.billType2==0">再次购买</div>
            </div>
            <div class="fixedBtnList" v-if="parentData.flag == 5">
                <div class="evaluateBtn" @click="goToMyEvaluation()">评价</div>
                <div class="evaluateBtn" @click="goToProduct(2)" v-show="OrderDetailData.billType2==0">再次购买</div>
            </div>
        </div>

        <div class="fixedBox-l" v-if="(parentData.flag!= 6&&parentData.flag!= 9)&&(notClick||parentData.flag != 1) && themeCurrent == '1'" :style="{paddingBottom:this.$footBarHeight+'px'}">
            <div class="relationBtn-l" v-if="OrderDetailData.shopList.length==1" @click="callProperty()">
                <img class="relationImg-l" src="@/assets/images/mall/call_small.png" alt="">
                <div class="relationTxt-l">联系商家</div>
            </div>
            <div class="relationBtn-l" v-if="OrderDetailData.shopList.length!=1">

            </div>
            <div class="fixedBtnList-l" v-if="parentData.flag == 1&&notClick">
                <div class="removeBtn-l" @click="orderCancel()">取消订单</div>
                <div class="gotopayBtn-l" @click="prepareToPayOrder()">去支付</div>
            </div>
            <div class="fixedBtnList-l" v-if="parentData.flag == 3&&notClick">
                <div class="gotopayBtn-l" @click="confirmOrder()">确认收货</div>
            </div>
            <div class="fixedBtnList-l" v-if="parentData.flag == 7&&notClick">
                <div class="removeBtn-l" @click="refundBySub()">申请退款</div>
            </div>
            <div class="fixedBtnList-l" v-if="parentData.flag == 4">
                <div class="evaluateBtn-l" @click="goToProduct(1)" v-show="OrderDetailData.billType2==0">再次购买</div>
            </div>
            <div class="fixedBtnList-l" v-if="parentData.flag == 5">
                <div class="evaluateBtn-l" @click="goToMyEvaluation()">评价</div>
                <div class="evaluateBtn-l" @click="goToProduct(2)" v-show="OrderDetailData.billType2==0">再次购买</div>
            </div>
        </div>
        <van-popup v-model="payWindowShow" position="bottom" class="vanPopup" @close="closePopup">
            <div class="payModeBox">
                <div class="payModeTitle">支付方式</div>
                <div v-show="isAcppay" class="payModeItem" @click="alipay(2)">
                    <div style="display: flex;align-items: center;height: 100%;">
                        <img src="@/assets/images/common/icon_unipay.png" alt="">
                        <div>云闪付</div>
                        <img class="iconUnipayRight" src="@/assets/images/common/icon_unipay_right.png" alt="">
                        <div class="saleIcon" v-if="isAcpPayTag">{{acpPayTag}}</div>
                    </div>
                </div>
                <div v-show="isAlipay" class="payModeItem" @click="alipay(1)">
                    <img src="@/assets/images/common/icon_alipay.png" alt="">
                    <div>支付宝</div>
                </div>
                <div v-show="isWechart" class="payModeItem" @click="alipay(0)">
                    <img src="@/assets/images/common/icon_wechart.png" alt="">
                    <div>微信</div>
                </div>
            </div>
            <div class="cancelBtn" @click="closePopup">取消</div>
        </van-popup>

    </div>
</template>

<script>
    import { getOrderDetailService,getEvaluateOrderDetailService, orderCancelService, confirmOrderService, refundBySubService,batchAddGoodsService, deleteOrderService, exchangeIdService } from "../../services/goodsService";
    import { prepareToPayOrderService, orderDetailService, unionOrderPrepayService } from '../../services/productOrderService.js';
    import { getDadaOrderTrack } from "../../services/expressTrajectory";
    import { mapActions, mapState } from "vuex";
    import NavigationBar from '@/common/currency/NavigationBar.vue';
    import { Toast, Dialog } from "vant";

    export default {
        name: "orderDetails",
        data() {
            return {
                notClick: true, //是否进行操作
                payCountDown: '',//倒计时
                parentData: {}, //列表传来的数据
                payWindowShow: false,//去支付弹窗
                isAlipay: false,//是否支付宝支付
                isWechart: false,//是否微信支付
                isAcppay:false,//云闪付支付选择
                acpPayTag:'',//满减标签
                isAcpPayTag:false,//是否有满减标签
                payParam: {},//支付参数
                OrderDetailData: {
                    shopList: [],
                }, // 订单详情
                timePromise:null, //计时器
                orderDataIsReady:false,
                title:'订单详情',
                imgIcon: {
                    titleImg1: require("@/assets/images/mall/mall_lab_1.png"),
                    titleImg2: require("@/assets/images/mall/mall_lab_2.png"),
                },
                stateTxt: ["", "等待付款", "已取消", "商家已发货", "交易成功", "等待评价", "订单签收"],
                themeCurrent: 0,
                trackData:{},
                expressStatusMsgInFirstDiv:'',
                serviceMsg:'',
                arriveTime:'',
                subBillNo:'',
                lineImg:require("@/assets/images/mall/express/line_cut.png"),
                rightArrowImg:require("@/assets/images/home/property_payment/icon_arrow.png"),
                hasExpressStatus:false,
            }
        },
        created() {
        },
        components:{
            NavigationBar
        },
        mounted() {
            // this.parentData = this.$route.query.object;
            // this.notClick = true;
            // this.getOrderDetail();
            //监听ios支付结果 检测当前订单状态


        },
        activated() {
            this.themeCurrent = this.$curTheme;
            if (this.$route.params.object) {
                this.parentData = [];
                this.OrderDetailData = {
                    shopList: [],
                };
                this.payCountDown='';
                clearInterval(this.timePromise);//终止倒计时
                this.parentData = this.$route.params.object;
                this.hasExpressStatus=this.$route.params.object.hasExpressStatus;
                console.log("======="+this.hasExpressStatus);
                console.log("+++++++"+this.parentData.flag);
                this.notClick = true;
                this.subBillNo=this.parentData.subBillNo;
                this.expressStatusMsgInFirstDiv=' ';
                this.serviceMsg=' ';
                this.getDadaOrderTrackService(this.parentData.subBillNo);
                this.orderDataIsReady=false;
                if(this.parentData.flag == 5){
                    // this.getEvaluateOrderDetail();
                    this.getOrderDetail();
                }else if (this.parentData.flag == 3){
                    this.getOrderDetail();
                }else {
                    this.getOrderDetail();
                }
                window.scrollTo(0, 0);
            } else {

            }
        },
        props: {},
        watch: {
            /*监控到物理返回键点击状态*/
            orderDetailEventBackButtonFlag(val) {
            console.log("orderDetailEventBackButtonFlag")

            if (val) {
                console.log("orderDetailEventBackButtonFlag");
                this.$store.dispatch('setOrderDetailEventBackButtonFlag', false);
                if(this.payWindowShow){
                    this.payWindowShow = false;
                }else{
                    this.$router.go(-1);
                }

            }
      }

        },
        computed: mapState({
            pageNumber: state => state.productPage.shopGoodsPageNumber,
            orderDetailEventBackButtonFlag: state => state.goBack.orderDetailEventBackButtonFlag

        }),
        methods: {
            /**
             * 获取订单详情
             * @param
             * ownerCode Number
             * billNo Number
             * subBillNo Number
             */
            getOrderDetail() {
                let paramData = {
                    ownerCode: this.parentData.ownerCode,
                    billNo: this.parentData.billNo,
                    subBillNo: this.parentData.subBillNo,
                }
                getOrderDetailService(paramData).then(res => {
                    this.orderDataIsReady=true;
                    if (res.resultCode == 0) {
                        // this.OrderDetailData = res.data;
                        // if (this.parentData.flag == 5) {
                        //     for (let i = 0; i < res.data.shopList[0].skuList.length; i++) {
                        //         if (res.data.shopList[0].skuList[i].skuNo == this.parentData.item.skuInfo.skuNo) {
                        //             res.data.shopList[0].skuList = [res.data.shopList[0].skuList[i]];
                        //             this.OrderDetailData = res.data;
                        //             return;
                        //         }
                        //     }
                        // } else {
                            this.OrderDetailData = res.data;
                            if (this.parentData.flag == 1) {
                                this.setPayTime(this.OrderDetailData.createdTime, this.OrderDetailData.serverTime,this.OrderDetailData.billType2)
                            } else if (this.parentData.flag == 3||this.parentData.flag == 7) {
                                this.setReciveTime(this.OrderDetailData.payTime, this.OrderDetailData.serverTime)
                            }
                        // }
                    }
                })
                // .catch(error => {
                // });
            },

            /**
             * 获取待评价订单详情
             * @param
             * ownerCode Number
             * billNo Number
             * subBillNo Number
             */
            getEvaluateOrderDetail() {
                let paramData = {
                    ownerCode: this.parentData.ownerCode,
                    billNo: this.parentData.subBillNo,
                    skuNo: this.parentData.item.skuInfo.skuNo,
                }
                getEvaluateOrderDetailService(paramData).then(res => {
                    if (res.resultCode == 0) {
                        // this.OrderDetailData = res.data;
                        if (this.parentData.flag == 5) {
                            for (let i = 0; i < res.data.shopList[0].skuList.length; i++) {
                                if (res.data.shopList[0].skuList[i].skuNo == this.parentData.item.skuInfo.skuNo) {
                                    res.data.shopList[0].skuList = [res.data.shopList[0].skuList[i]];
                                    this.OrderDetailData = res.data;
                                    return;
                                }
                            }
                        } else {
                            this.OrderDetailData = res.data;
                            if (this.parentData.flag == 1) {
                                this.setPayTime(this.OrderDetailData.createdTime, this.OrderDetailData.serverTime,this.OrderDetailData.billType2)
                            } else if (this.parentData.flag == 3) {
                                this.setReciveTime(this.OrderDetailData.payTime, this.OrderDetailData.serverTime)
                            }
                        }
                    }
                }).catch(error => {
                });
            },
            //复制功能
            copyBtn(copyNum) {
                cordova.plugins.clipboard.copy(copyNum, function () {
                    Toast("复制成功");
                }, function () {
                    console.log("error");
                });
            },
            //未付款支付倒计时
            setPayTime(createTime, serverTime,billType) {
                let that = this;
                let timePromise = null;
                let createTimes = "";//订单创建时间
                let serverTimes = "";//订单创建时间
                let totalSecond = 0;//设置计时总时间（分钟）
                if (createTime != null) {
                    //为了支持safari浏览器
                    createTimes = new Date(createTime.replace(/\-/g, "/")).getTime();
                    serverTimes = new Date(serverTime.replace(/\-/g, "/")).getTime();
                    if(billType==0){
                        totalSecond = Math.round((createTimes + 30 * 60 * 1000 - serverTimes) / 1000);
                    }else{
                        totalSecond = Math.round((createTimes + 15 * 60 * 1000 - serverTimes) / 1000);
                    }
                } else {
                    totalSecond = 30 * 60;
                }
                if(totalSecond >= 0){
                    //倒计时
                    this.timePromise = setInterval(function () {
                        if (totalSecond >= 0) {
                            var t1 = Math.floor(totalSecond / 60);
                            var m = t1 < 10 ? "0" + t1 : t1;
                            var t2 = totalSecond - t1 * 60;
                            var s = t2 < 10 ? "0" + t2 : t2;
                            totalSecond = totalSecond - 1;
                            that.payCountDown = m + "分钟" + s + "秒";
                        } else {
                            that.notClick = false;
                            clearInterval(this.timePromise);//终止倒计时
                        }
                    }, 1000);
                }else{
                    that.notClick = false;
                }

            },
            //待收货支付倒计时
            setReciveTime(createTime, serverTime) {
                let createTimes = "";//订单创建时间
                let serverTimes = "";//订单创建时间
                let totalSecond = 0;//设置计时总时间（分钟）
                if (createTime != null) {
                    //为了支持safari浏览器
                    createTimes = new Date(createTime.replace(/\-/g, "/")).getTime() / 3600000;
                    serverTimes = new Date(serverTime.replace(/\-/g, "/")).getTime() / 3600000;
                    totalSecond = Math.round(createTimes + 336 - serverTimes);
                } else {
                    totalSecond = 336;
                }
                if (totalSecond >= 0) {
                    var d1 = Math.floor(totalSecond / 24);
                    var d = d1 < 10 ? "0" + d1 : d1;
                    var m2 = totalSecond - d1 * 24;
                    var m = m2 < 10 ? "0" + m2 : m2;
                    totalSecond = totalSecond - 1;
                    this.payCountDown = d + "天" + m + "时";
                } else {
                    this.payCountDown = 0 + "天" + 0 + "时";
                }
            },
            /**
             * 去支付按钮
             * @params
             * billNo Number 订单编号
             */
            prepareToPayOrder() {
                let paramData = {
                    billNo: this.parentData.billNo,
                }
                prepareToPayOrderService(paramData).then(res => {
                    if (res.resultCode == 0) {
                        console.log(res)
                        this.payParam = res.data;
                        if (1 == this.payParam.IS_ALI_PAY) {//判断是否支持微信或者支付宝支付
                            this.isAlipay = true;
                        } else {
                            this.isAlipay = false;
                        };
                        if (1 == this.payParam.IS_WX_PAY) {
                            this.isWechart = true;
                        } else {
                            this.isWechart = false;
                        };
                        if (1 == this.payParam.IS_ACP_PAY) {
                            this.isAcppay = true;
                        } else {
                            this.isAcppay = false;
                        };
                        if (this.payParam.acpPayTag != "" && this.payParam.acpPayTag != undefined &&this.payParam.acpPayTag != null){
                            this.acpPayTag = this.payParam.acpPayTag;
                            this.isAcpPayTag = true;
                        }
                        this.payWindowShow = true;

                    }else{
                    }
                }).catch(error => {
                         Dialog.alert({
                            message:error
                            }).then(() => {

                        });
                });
            },


            //关闭去支付弹窗
            closePopup() {
                this.payWindowShow = false;
            },
            /*银联下单支付*/
            alipay(index) {//x订单编码//y支付金额,index 0 微信 1 支付宝 p 支付参数
                if (index ===2){
                    let pram = {
                        'merOrderId':this.payParam.merOrderId,
                        'payType':2
                    }
                    unionOrderPrepayService(pram).then(res =>{
                        console.log(res);
                        let unipay = cordova.plugins.LantuUnionPay;
                        //payMode 00 正式环境，01测试环境 正式版本需要切换
                        unipay.pay({tn:res.data.tn,payMode:this.payParam.acpGatewayType},(success)=>{
                            console.log("支付成功回调"+JSON.stringify(success));
                            this.getOrderPayDetail();
                        },(error)=>{
                            console.log("支付失败回调"+JSON.stringify(error));
                            this.getOrderPayDetail();
                        });
                    }).catch(error => {
                        console.log(error);
                    });
                }else {
                    this.payParam.payWay = index;
                    this.payWindowShow = false;
                    //监听ios支付结果 检测当前订单状态
                    if (this.$devicePlatform != "pc") {
                        console.log("ios++addEventListener");

                        window.broadcaster.addEventListener("orderDetailPayBack", this.payCallBack);
                    }

                    window.broadcaster.fireNativeEvent("ionicToNative", {
                        DATA: this.payParam,
                        FLAG: '789'
                    }, function () {
                    })
                }
            },
            //支付完成后回调
            payCallBack() {
                this.getOrderPayDetail();
            },


            // 支付完成后获取订单详情查询订单状态
            getOrderPayDetail: function () {
                let data = {
                    billNo: this.parentData.billNo,
                    ownerCode: this.$userInfo.OWNER_CODE
                }
                console.log("getOrderPayDetail");
                orderDetailService(data).then(res => {

                    if ("0" == res.resultCode) {//true表示返回成功
                        if (res.data.billStatus == "02") {

                            /* 取消监听，避免重复监听 */
                            if (this.$devicePlatform != "pc") {
                                window.broadcaster.removeEventListener("orderDetailPayBack",this.payCallBack);
                            }
                            this.$router.push({ name: "paySuccess", params: { orderData: res.data } });
                            console.log("支付成功");
                        } else {
                            //其他情况跳转订单列表页
                            console.log("支付失败");
                        }
                    } else {
                        //其他情况跳转订单列表页
                        console.log("查询订单状态失败");
                    }
                }).catch(error => {
                });
            },





            // 是否取消订单
            orderCancel() {
                Dialog.confirm({
                    message: '确认取消该订单？',
                    confirmButtonText: "确认取消",
                    cancelButtonText:"我再想想"
                }).then(() => {
                    // on confirm
                    this.orderCancelRequest();
                }).catch(() => {
                    // on cancel
                });

            },
            /**
             * 取消订单
             * @param
             * billNo Number  订单号
             */
            orderCancelRequest() {
                let paramData = {
                    billNo: this.parentData.billNo
                }
                orderCancelService(paramData).then(res => {
                    if (res.resultCode == 0) {
                        this.notClick = false;
                        this.goToBack();
                    }
                }).catch(error => {
                });
            },
            /**
             * 确认收货
             * @param
             * subBillNo Number  子订单号
             * signerType Number  业主标识
             * signerCode Number  业主CODE
             */
            confirmOrder() {
                Dialog.confirm({
                    message: '您确认收到该商品了？',
                    confirmButtonText: "确认收货",
                    cancelButtonText:"取消"
                }).then(() => {
                    // on confirm
                    let paramData = {
                        subBillNo: this.parentData.subBillNo,
                        signerType: '01',
                        signerCode: this.$userInfo.OWNER_CODE
                    }
                    confirmOrderService(paramData).then(res => {
                        if (res.resultCode == 0) {
                            Toast('确认收货');
                            this.notClick = false;
                        }
                    }).catch(error => {
                    });
                }).catch(() => {
                    // on cancel
                });

            },
            /**
             * 申请退款
             * @param
             * subBillNo   Number  子订单号
             * ownerCode   Number  业主CODE
             */
            refundBySub(){
                Dialog.confirm({
                    title:"确认退款吗？",
                    message:"退款后券无法退回哦",
                    confirmButtonText: "确认退款",
                    cancelButtonText:"我再想想"
                }).then(() => {
                    let paramData = {
                        subBillNo: this.parentData.subBillNo,
                        ownerCode: this.$userInfo.OWNER_CODE
                    }
                    refundBySubService(paramData).then(res => {
                        if (res.resultCode == 0) {
                            this.notClick = false;
                            Toast('申请已提交');
                        }else{
                            Toast(res.msg);
                        }
                    }).catch(error => {
                    });
                })

            },
            //去评价页面
            goToMyEvaluation() {
                let paramsData = {
                    // spuPicUrl: this.OrderDetailData.shopList[0].skuList[0].spuPicUrl,
                    // spuCode: this.OrderDetailData.shopList[0].skuList[0].spuCode,
                    // shopCode: this.OrderDetailData.shopList[0].shopCode,
                    // orderCode: this.parentData.subBillNo,
                    // attrValueContent: this.OrderDetailData.shopList[0].skuList[0].attrValueContent
                    billItem:this.parentData.item
                }
                 this.$router.push({
                    name: 'myEvaluate', params: {
                    detail:paramsData
                    }
                });
                // console.log(paramsData)
            },
            //再次购买 index 1 已完成再次购买  2 待评价再次购买
            goToProduct(index) {
                let paramData = []

                for (let i = 0; i < this.parentData.item.shop.skuList.length; i++) {
                    paramData.push({
                        ownerCode: this.$userInfo.OWNER_CODE,
                        spuCode: this.parentData.item.shop.skuList[i].spuCode,
                        skuNo: this.parentData.item.shop.skuList[i].skuNo,
                        shopCode: this.parentData.item.shop.shopCode,
                        skuCount: this.parentData.item.shop.skuList[i].skuCount,
                        mark: 1
                    })
                }


                batchAddGoodsService(paramData).then(res => {
                    if (res.resultCode == 0) {
                        Toast({
                            message: '添加购物车成功',
                            duration:1000
                        });
                        this.$router.push({name:'shoppingCart',params:{data:paramData}});
                    }else{
                        Toast(res.msg)
                    }
             }).catch(error => {
                });
            },
            //删除该订单
            deleteOrder() {
                let paramData = {
                    subBillNo: this.parentData.subBillNo,
                    ownerCode: this.$userInfo.OWNER_CODE
                }
                deleteOrderService(paramData).then(res => {
                    if (res.resultCode == 0) {
                        Toast('删除成功');
                        this.notClick = false;
                        this.goToBack();
                    }
                }).catch(error => {
                });
            },
            //打电话
            callProperty() {
                // window.location.href = "tel:" + this.OrderDetailData.shopList[0].shopPhone;
                const platform = navigator.userAgent;
                const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
                if (isiOS) {
                window.location.href = "tel:" + this.OrderDetailData.shopList[0].shopPhone;
                } else {
                    Dialog.confirm({
                        title:"拨打电话",
                        message: this.OrderDetailData.shopList[0].shopPhone,
                    }).then(() => {
                        window.location.href = "tel:" + this.OrderDetailData.shopList[0].shopPhone;
                    }).catch(() => {
                        // on cancel
                        Dialog.close;
                    });
                }
            },
            //去店铺首页
            goToShopPage(shopCode) {
                this.$router.push({
                    name: 'shopPage',
                    params: {
                        shopCode: shopCode
                    }
                })

            },
            //去商品页
            goToGoodsDetails(skuItem, activitySpuId) {
                if (!activitySpuId) {
                    this.$router.push({
                        name: 'goodsDetail',
                        params: {
                            spuCode: skuItem.spuCode,
                            goodsUrl:skuItem.spuPicUrl
                        }
                    })
                } else {
                    console.log(activitySpuId)
                    let paramData = {
                        activitySpuId: activitySpuId
                    }
                    exchangeIdService(paramData).then(res => {
                        if (res.resultCode == 0) {
                            if (res.data.activitySpuId) {
                                this.$router.push({
                                    name: 'flashSaleGoods',
                                    query: {
                                        spuCode: skuItem.spuCode,
                                        activitySpuId: res.data.activitySpuId,
                                        status: res.data.activityStatus,
                                        goodsUrl:skuItem.spuPicUrl
                                    }
                                })
                            } else {
                                this.$router.push({
                                    name: 'goodsDetail',
                                    params: {
                                        spuCode: skuItem.spuCode
                                    }
                                })
                            }
                        }
                    }).catch(error => {
                    });
                }

            },
            getDadaOrderTrackService(subBillNo){
                getDadaOrderTrack(subBillNo).then( result => {
                    if (result.resultCode=="0"){
                        this.trackData = result.data;
                        this.shopInfo=result.data.shopInfoDTO;
                        this.shopPicurl=this.shopInfo.shopPicUrl;
                        this.historyStatusDTOList=result.data.historyStatusDTOList;
                        this.resolveTrackData(result.data);
                    }else {
                        console.log(result);
                        this.expressStatusMsgInFirstDiv='配送信息查询异常';
                        this.serviceMsg='请稍后再试';
                    }
                }).catch(error => {
                    console.log("出错了 ： "+error);
                    this.hasExpressStatus=false;
                });
            },
            resolveTrackData(trackData){
                switch (trackData.statusCode) {
                    case "1":
                        this.expressStatusMsgInFirstDiv='等待配送员接单';
                        this.serviceMsg='正在为您紧急召唤配送员';
                        break;
                    case "2":
                        this.expressStatusMsgInFirstDiv='配送员已接单';
                        this.serviceMsg='配送员正在赶往商家';
                        break;
                    case "3":
                        this.expressStatusMsgInFirstDiv='正在配送中';
                        this.serviceMsg='开始为您配送，请耐心等待';
                        break;
                    case "4":
                        this.expressStatusMsgInFirstDiv='配送已完成';
                        this.serviceMsg='本次配送已完成，感谢您的信任，欢迎再次选购！';
                        break;
                    case "5":
                        this.expressStatusMsgInFirstDiv='等待配送员接单';
                        this.serviceMsg='正在为您紧急召唤配送员';
                        break;
                    case "9":
                        this.expressStatusMsgInFirstDiv='妥投异常';
                        this.serviceMsg='配送员无法与您取得联系，正在将商品返回商家';
                        break;
                    case "10":
                        this.expressStatusMsgInFirstDiv='妥投异常';
                        this.serviceMsg='配送员无法与您取得联系，商品已返回商家';
                        break;
                    case "100":
                        this.expressStatusMsgInFirstDiv='配送员已到店';
                        this.serviceMsg='配送员已到店，正在取货中';
                        break;
                    default :
                        break;
                }
            },
            //查看物流信息
            gotoMapforExpress(){
                // '314378699124482048C2',this.$userInfo.AREA_CODE
                this.$router.push({ name: "mapForExpress", params: { subBillNo: this.subBillNo } });
            },
            //返回上一页
            goToBack() {
                this.$router.go(-1);
            }
        }
    }
</script>

